<script>
  let called = 0
  export default {
    props:{
      tag: {default:'div'}
    },
    beforeCreate(){
      console.log('%c 3. childslot:beforecreate','font-size: 20px;color:#b4909c');
    },
    created(){

      console.log('%c 3. childslot:created','font-size: 20px;color:#b4909c');
    },
    beforeMount(){

      console.log('%c 3. childslot:beforemount','font-size: 20px;color:#b4909c');
    },
    mounted(){
      console.log('%c 3. childslot:mounted','font-size: 20px;color:#b4909c');
    },
    beforeDestroy (){
      console.log('%c 3. childslot:beforedestroy','font-size: 20px;color:#b4909c');
    },
    destroyed(){
      console.log('%c 3. childslot:destroyed','font-size: 20px;color:#b4909c');
    },
    render(h){
      return h(this.tag,{class:['child-slot']},[
        this.$slots.default,
        h('h1','childslot called:'+ ++called)])
    }
  }
</script>


