<script>
  let called = 0
  import Vue from 'vue'
  import Child from './Child'
  import Sibling from './Sibling'
  import ChildSlot from './ChildSlot'
  export default {
    data(){
      return {
        isFirst: true
      }
    },
    beforeCreate(){
      console.log('%c * parent:beforecreate', 'font-size: 21px;color:blue');
    },
    created(){

      console.log('%c * parent:created', 'font-size: 21px;color:blue');
    },
    beforeMount(){

      console.log('%c * parent:beforemount', 'font-size: 21px;color:blue');
    },
    mounted(){
      console.log('%c * parent:mounted', 'font-size: 21px;color:blue');
    },
    beforeDestroy (){
      console.log('%c * parent:beforedestroy', 'font-size: 21px;color:blue');
    },
    destroyed(){
      console.log('%c * parent:destroyed', 'font-size: 21px;color:blue');
    },
    components: {
      'child': Child,
      'sibling': Sibling,
      'child-slot': ChildSlot
    },
    render(h) {
      return h('div', { class: [ 'parent' ] },
        [ h('h1', 'parent called: ' + ++called),
          h('sibling'),
          h('child',{attrs:{tag:'section'}}),
          h('child-slot',[ h('p', 'I\'m from parent') ])
        ]
      )
    }
  }
</script>


