<script>
  let called= 0
  export default {
    beforeCreate(){
      console.log('%c 1. sibling:beforecreate', 'font-size: 20px;color:#64909c');
    },
    created(){

      console.log('%c 1. sibling:created', 'font-size: 20px;color:#64909c');
    },
    beforeMount(){

      console.log('%c 1. sibling:beforemount', 'font-size: 20px;color:#64909c');
    },
    mounted(){
      console.log('%c 1. sibling:mounted', 'font-size: 20px;color:#64909c');
    },
    render (h){
      return h('div',{class:['sibling']},
        [h('h1',`sibling called: ${++called}`)]);
    }
  }
</script>


